import { Component } from '@angular/core';
import { MoltinService } from '../../shared/services/moltin.service';

@Component({
  selector: 'product-list',
  templateUrl: './product-list.component.html',
  styleUrls: ['./product-list.component.scss'],
})

export class ProductListComponent {
  private products;

  constructor(private moltin: MoltinService){
    moltin.products().then(data => { this.products = data; console.log(data[0]) });
  }
}



