import { Injectable } from '@angular/core';
import { environment } from '../../../environments/environment';
import { HttpHeaders, HttpClient, HttpParams } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';

import { ErrorObservable } from 'rxjs/observable/ErrorObservable';
import { catchError } from 'rxjs/operators/catchError';
import { gateway as MoltinGateway } from '@moltin/sdk';

const Moltin = MoltinGateway({
  client_id: 'utqYigHjdKwr8unViEhWVj82HqkM5sZahWMI5U9JZr'
});

@Injectable()
export class MoltinService {

  constructor(
    private http: HttpClient,
  ) {

  }

  products() {
    return new Promise((resolve, reject) => {
      Moltin.Products.With('main_image').Limit(8).All().then((products) => {
        // Do something
        let data = products.data;
        let images = products.included.main_images;

        for (let p of data) {
          if (typeof p.relationships.main_image !== 'undefined') {
            for (let i of images) {
              if (i.id === p.relationships.main_image.data.id) {
                p.main_image_href = i.link.href;
              }
            }
          }
        }

        resolve(products.data);
      });
    })
  }
  
}



